// COLORS
$white: #fff;
$main: #003087;
$secondary: #41b6e6;

// SPACING

//BREAKPOINTS
$lg-width-min: 992px;
$xl-width-min: 1200px;

$text-arrow-space: 16px;
$shaft-width: 1px;
$newshaft-width: 18px;
$shaft-thickness: 1px;
$arrow-head-width: 8px;
$arrow-head-thickness: $shaft-thickness;

.explore-button {
  display: block;
  position: relative;
  margin: 1rem auto;
  border-radius: 24px;
  border: 1px solid transparent;
  background-color: $main;
  padding: 12px 22px;
  width: 115px;
  transition: background-color ease 0.3s;
  color: $white;
  text-decoration: none;

  &:hover {
    background-color: $secondary;
  }

  .arrow {
    position: absolute;
    top: 20px;
    width: 17px;
    height: 2px;
    right: 20px;
    background-color: #fff;

    &::after,
    &::before {
      content: '';
      position: absolute;
      width: 11px;
      height: 2px;
      right: -2px;
      background-color: #fff;
    }

    &::after {
      top: -4px;
      transform: rotate(45deg);
    }

    &::before {
      top: 4px;
      transform: rotate(-45deg);
    }
  }

  @media (min-width: $lg-width-min) {
    margin: 2rem auto;
  }
}
